@import 'variables';

.scenario-form-wrapper {
    border-top: 1px solid #222;

    #scenario-form {
        padding: 0.5rem;

        .scenario-view-hidden {
            display: none !important;
        }

        .info-wrapper {
            h1 {
                color: $pastel-blue;
            }
        }

        #basic-info {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            h1 {
                color: $pastel-blue;
            }

            label {
                display: flex;
                flex-direction: column;
                font-weight: 500;
            }
        }

        #sample,
        #opqrst {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            label {
                display: flex;

                input {
                    display: inline-block;
                    margin: 0 1rem;
                    width: 100%;
                }
            }
        }

        #medications {
            // display: flex;
            // flex-direction: column;
            // gap: 0.5rem;

            .meds-wrapper {
                // display: grid;
                // grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
                // gap: 0.5rem;
                table {
                    width: 100%;
                    th {
                        font-weight: bold;
                    }
                    thead {
                        th {
                            text-align: center;
                        }
                    }
                    tbody {
                        input {
                            width: 100%;
                        }
                    }

                }

                label {
                    // display: flex;
                    // gap: 1em;
                    border: 1px solid #ddd;
                    border-style: none none solid none;
                    padding: 0.5em;

                    input {
                        max-width: 15em;
                        margin-left: auto;
                        margin-right: 3em;
                    }
                }
            }
        }

        .vitals {
            // display: grid;
            // grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
            // gap: 1rem;
            table {
                width: 100%;
                 th {
                        font-weight: bold;
                    }
                thead {
                    th {
                        text-align: center;
                    }
                }
                tbody {
                    input {
                        width: 100%;
                    }
                }
            }
            .vital-wrapper {
                border: 1px solid $header-color;
                border-radius: 0.5rem;
                padding: 0.5rem;
                max-width: calc(100vw - 1rem);

                h1 {
                    color: $pastel-blue;
                }

                .vitals-box {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    gap: 1rem;

                    label {
                        display: flex;
                        font-weight: 500;

                        input {
                            display: inline-block;
                            margin: 0 0 0 1rem;
                            width: 100%;
                        }
                    }
                }
            }
        }

        #scenario-steps {
            display: flex;
            flex-direction: column;

            h1 {
                color: $pastel-blue;
            }

            .header-item {
                h2 {
                    color: $pastel-blue;
                }
            }

            .step-item {
                border: 1px solid #333;
                border-style: none none solid none;
                padding: 0.5rem;

                &:nth-child(odd) {
                    background-color: #eee;
                }

                label {
                    display: flex;
                    align-items: center;

                    input {
                        display: inline-block;
                        max-height: min-content;
                        align-self: flex-end;
                        margin-left: 1em;
                    }
                }
            }
        }
    }

    #tab-selector {
        display: flex;
        flex-flow: row wrap;
        background-color: $header-color;

        button {
            all: unset;
        }

        .active-tab {
            background-color: $header-dark;
        }

        .tab-button {
            color: #ddd;
            cursor: pointer;
            padding: 0.75em 1.25em;

            &:hover {
                background-color: $header-dark;
            }
        }

        #submit-next {
            background-color: $pastel-blue;
            padding: 0.75em 1.25em;
            color: #ddd;
            margin-left: auto;
            cursor: pointer;
        }
    }
}