@import 'variables';

.home-nav-wrapper {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    
    .home-nav-link {
        border-color: $header-color;
        border-width: 2px 2px 2px 0.75em;
        border-style: solid;
        border-radius: 0.25rem;
        padding: 0.3em 0.6em;
        font-size: 3em;

        &:hover {
            background-color: #ddd;
        }
    }
}