@import 'variables';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 300;
    text-decoration: none;
    color: #333;
}

#root {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

@mixin bttn {
    display: inline-block;
    border: 1px solid;
    border-radius: 5px;
    padding: 0.6em 1em;
    cursor: pointer;
    font-size: 1em;
    white-space: nowrap;
    background-color: #fff;
}

button {
    @include bttn;
}

input[type="text"],
input[type="number"],
input[type="password"],
textarea {
    padding: 0.25em;
}


header {
    background-color: $header-color;
    display: flex;
    align-items: center;
    padding: 1rem;
    gap: 1rem;

    h1 {
        color: #ddd;
        cursor: pointer;
        font-size: 2.5em;
    }

    nav {
        margin-left: auto;
        display: flex;
        flex-wrap: wrap;
        gap: 1em;

        button {
            color: $header-color;
            background-color: #ddd;
            margin-left: auto;
            border: none;
        }

        .sign-out-bttn {
            color: #ddd;
            background-color: rgb(150, 0, 0);
        }
    }
}

.hint-text {
    font-style: italic;
    color: #999;
}

.db-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    margin: 1rem 0;
    gap: 1rem;
    padding: 0 5rem;
    
    .new-bttn {
        font-style: italic;
        
        &:hover {
            background-color: #ddd;
        }
    }
    
    .db-list-item {
        display: flex;
        border: 2px solid $header-color;
        border-radius: 0.5rem;
        align-items: center;
        flex-wrap: wrap;
        gap: 1rem;
        padding: 1rem;
        width: 100%;
        min-width: min-content;

        .list-button-wrapper {
            display: flex;
            gap: 0.5rem;
            margin-left: auto;

            button {
                all: unset;
            }

            .action-bttn {
                @include bttn;
                border: 1px solid #ddd;

                &:hover {
                    background-color: #ddd;
                }
            }

            .delete-bttn {
                border-color: rgb(150, 0, 0);
                color: rgb(150, 0, 0);

                &:hover {
                    background-color: rgb(150, 0, 0);
                    color: #fff;
                }
            }
        }
    }
}