@import 'variables';

.checklist-meta-data {
    .tab-selector {
        background-color: $header-color;
        display: flex;
        border: 1px solid $header-dark;
        border-style: solid none none none;
        flex-wrap: wrap;

        .tab {
            color: #eee;
            padding: 0.5rem 1rem;
            cursor: pointer;

            &:hover {
                background-color: $header-dark;
            }
        }
        
        .selected {
            background-color: $header-dark;
        }
    }

    .hidden {
        display: none;
    }

    .meta-block {
        border: 1px solid #333;
        border-style: none none solid none;
        padding: 0.75rem;

        .relation-select {
            padding: 3px 0;
            display: flex;
            gap: 5px;
        }

        .to-swap,
        .to-swap:hover {
            background-color: aquamarine;
        }
        
        .swap-bbtn:hover {
            background-color: aquamarine;
        }
    }

    .meta-item,
    .meta-item-option {
        display: flex;
        border: none;
        padding: 2px;
        gap: 2px;

        p {
            margin-right: 0.5rem;
        }

        input[type="text"] {
            width: 25%;
        }

        select {
            width: 15%;
        }

        button {
            all: unset;
            background-color: #eee;
            padding: 0 1em;
            cursor: pointer;
             
            &:hover {
                background-color: aquamarine;
            }
        }
    }

    .meta-data-hierarchy {
        padding: 0.5rem;

        .hierarchy-block {
            display: flex;
            flex-direction: column;
            gap: 0.25rem;
        
            .hierarchy-item {
                margin-left: 2rem;
                display: flex;
                gap: 0.5rem;
            }

            button {
                all: unset;
                background-color: #eee;
                padding: 0 1em;
                cursor: pointer;
                
                &:hover {
                    background-color: aquamarine;
                }
            }
        }
    }

    button {
        margin: 1rem 0 1rem 1rem;

        &:hover {
            background-color: #ddd;
        }
    }
}