@import 'variables';

.scenario-assignment {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin: 1rem 0;
    padding: 1rem;

    * input {
        margin-left: 0.5rem;
    }

    .assignment-list {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        label {
            display: flex;
            align-items: center;
            gap: 1rem;

            input[type="number"] {
                width: 5em;
            }

            span {
                margin-left: auto;
                white-space: nowrap;
            }
        }
    }

    button {
        background-color: $light-blue;
    }
}