@import 'variables';

.mdb-form-wrapper {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    
    #debriefer-form {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: flex-start;

        .mdb-block-heading {
            font-weight: 500;
        }

        input[type="text"] {
            width: 100%;
        }

        label {
            display: flex;
            align-items: center;
            width: 100%;
            gap: 1rem;
            white-space: nowrap;
            
            button {
                height: 2em;
                padding: 0 1em;
                text-align: center;
            }
        }

        .mdb-block-wrapper {
            border: 1px solid $header-color;
            border-radius: 0.5rem;
            padding: 0.5rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .layer {
                display: flex;
                flex-flow: row wrap;
                gap: 1rem;
            }

            .mdb-block {
                border: 1px solid #ddd;
                border-radius: 0.5rem;
                padding: 0.5rem;

                display: flex;
                flex-direction: column;
                flex: 1 0 30%;
                align-items: flex-start;
                gap: 0.5rem;

                .mdb-block-item {
                    position: relative;

                    .search-results {
                        z-index: 999;
                        position: absolute;
                        top: 100%;
                        max-height: 10rem;
                        width: 25em;
                        overflow: auto;
                        background-color: #fff;
                        border: 1px solid $header-color;
                        
                        p {
                            padding: 0.1em 0.3em;
                            cursor: pointer;
                            overflow: hidden;
                            text-overflow: ellipsis;

                            &:hover {
                                background-color: #eee;
                            }
                        }
                    }
                }

                .mdb-red-item {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    gap: 0.25rem;

                    .mdb-red-type {
                        display: flex;
                        gap: 1rem;

                        .red-type {
                            width: min-content;
                        }
                    }
                }
            }

            #mdb-findings,
            #mdb-notes {
                background-color: rgba(0, 0, 0, 0.1);
            }

            #mdb-green {
                background-color: rgba(0, 255, 0, 0.3);
            }

            #mdb-yellow {
                background-color: rgba(255, 255, 0, 0.3);
            }

            #mdb-red {
                background-color: rgba(255, 0, 0, 0.3);
            }

            .mdb-info-wrapper {
                display: flex;
                gap: 1rem;
                flex-flow: row wrap;

                label {
                    flex: 0 15%;
                }
            }
        }
    }

    button {
        border: 1px solid #ddd;

        &:hover {
            background-color: #ddd;
        }
    }

    #submit-bttn {
        margin-left: auto;
        background-color: $light-blue;
    }
}

/**********************************
 *                                *
 *  CRITICAL ACTION ASSOCIATIONS  *
 *                                *
 **********************************/

#associations-wrapper {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;

    #associations-list {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .associations-and,
        .associations-or {
            display: flex;
            flex-direction: column;
            gap: 5px;
            align-items: flex-start;

            select, label{
                margin-left: 1rem;
            }

            label {
                display: flex;
                gap: 0.5rem;
            }
        }
    }

    #associations-submit {
        background-color: $light-blue;
        margin-top: 1rem;
        border-color: #ddd;
        color: #fff;
    }
}